body {
	display: flex;
	margin: 0;

	overflow: hidden;
}

div {
	height: 100vh;
	width: 100%;
}

#showSettings {
	position: fixed;
	top: 0;
	right: 0;

	display: none;
	height: 100vh;
	width: 20px;

	background: transparent;
	cursor: pointer;
	transition: background .3s;
}
#showSettings:hover {
	background: hsla(0, 0%, 0%, 0.3);
}

#gradienT {
	position: static !important;

	max-width: 300px;
	min-width: 300px;

	transition: max-width .3s, min-width .3s;
} 

.move {
	animation: move .5s ease-in-out;
}

.moveleft {
	animation: moveleft .5s ease-in-out;
}

.fade {
	animation: fade .3s ease-in-out;
}

@keyframes fade {
	0% {
		opacity: 0;
	}

	100% {
		opacity: 1;
	}
}

@keyframes move {
	0% {
		transform: translateX(-100%);
	}

	50% {
		transform: translateX(30px);
	}

	100% {
		transform: translateX(0);
	}
}

@keyframes moveleft {
	0% {
		transform: translateX(100%);
	}

	50% {
		transform: translateX(-30px);
	}

	100% {
		transform: translateX(0);
	}
}